.researchersSection {
  background-color: rgba(255, 255, 255, 1);
  display: flex;
  flex-direction: column;
  padding-bottom: 100px;
}

.sectionTitle {
  color: var(--NormalColor);
  text-align: center;
  text-transform: uppercase;
  font: 600 45px/1 Poppins, sans-serif;
  margin-bottom: 47px;
}

.researchersGrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
}

.researcherCard {
  background-color: #fff;
  border: 1px solid #ddd;
  display: flex;
  padding: 0 36px 0 1px;
}

.imageWrapper {
  width: 39%;
  border-right: 1px solid #ddd;
}

.researcherImage {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.researcherInfo {
  width: 61%;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.researcherName {
  color: #033b84;
  font-size: 25px;
  font-weight: 500;
  line-height: 1;
  margin-bottom: 9px;
}

.researcherTitle {
  color: #c2c2c2;
  font-weight: 500;
  line-height: 1;
  margin-bottom: 36px;
}

.researcherDetails {
  list-style-type: none;
  padding: 0;
  margin: 0;
  border-top: 1px solid #ddd;
  padding-top: 35px;
}

.detailItem {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.icon {
  width: 27px;
  height: 27px;
  margin-right: 12px;
}

.detailLabel {
  font-weight: 700;
  margin-right: 12px;
  color: #616161;
}

.detailValue {
  font-weight: 300;
}

@media (max-width: 1170px) {
  .researchersSection {
    padding: 100px 0;
  }

  .sectionTitle {
    font-size: 40px;
  }

  .researchersGrid {
    grid-template-columns: 1fr;
  }

  .researcherCard {
    flex-direction: column;
    padding: 20px;
  }

  .imageWrapper,
  .researcherInfo {
    width: 100%;
  }

  .imageWrapper {
    border-right: none;
    border-bottom: 1px solid #ddd;
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
}
.professorsGrid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 30px;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}