.container {
  background: var(--127001-linear-havelock-blue-wisteria, linear-gradient(var(--HeaderGradientAngle, 90deg), #74b1a2 0%, var(--HeaderGradientSecond, #1A1A2E) 100%));
  display: flex;
  flex-direction: column;
  overflow: hidden;
  color: #FFFFFF;
  text-transform: uppercase;
  font: 600 50px/1 Poppins, sans-serif;
}
@media (max-width: 991px) {
  .container {
    font-size: 40px;
  }
}

.heroSection {
  display: flex;
  flex-direction: column;
  background-blend-mode: soft-light;
  position: relative;
  height: 237px;
  width: 100%;
  align-items: start;
  padding: 140px 80px 190px;

  overflow: hidden;
}

@media (max-width: 991px) {
  .heroSection {
    max-width: 100%;
    font-size: 40px;
    padding: 100px 20px;
  }
}

.backgroundImage {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
  mix-blend-mode: soft-light;
}

.contentWrapper {
  position: relative;
  display: flex;
  margin-bottom: 58px;
  width: 746px;
  width: 100%;
  flex-direction: row-reverse;
}
.innerWrapper {
  flex-direction: column;
}

@media (max-width: 991px) {
  .contentWrapper {
    margin-bottom: 30px;
    font-size: 40px;
  }
}

.decorativeLine {
  background-color: #FFFFFF;
  width: 125px;
  height: 13px;
  left: -30px;

}



.title {
  margin-top: 20px;
  text-shadow: 2px 2px 8px rgba(170, 170, 170, 0.5); /* 그림자 설정 */
}

@media (max-width: 991px) {
  .title {
    max-width: 100%;
    margin-top: 40px;
    font-size: 59px;
    text-shadow: 2px 2px 6px rgba(170, 170, 170, 0.5); /* 반응형에 맞춘 그림자 설정 */
  }
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}