.hero {
  background: linear-gradient(135deg, #ffdfdf, #9B59B6);
  color: white;
  text-align: center;
  padding: 100px 20px;
  padding-top: 160px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 60vh;
  position: relative;
}

.heroContent {
  max-width: 800px;
  margin: 0 auto;
  animation: fadeIn 2s ease-in-out;
}

.heroTitle {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.heroText {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.imageSlider {
  width: 100%;
  height: 400px;
  margin: 40px 0;
  overflow: hidden;
}

.container {
  max-width: 800px;
  margin: 40px auto;
  padding: 0 20px;
}

.section {
  margin-bottom: 40px;
}

.sectionHeader {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.icon {
  font-size: 2rem;
  margin-right: 10px;
  color: #4A90E2;
}

.sectionTitle {
  font-size: 1.8rem;
  color: #4A90E2;
}

.sectionText {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 10px;
}

.researchList {
  list-style: none;
  padding-left: 0;
  margin-top: 10px;
}

.researchList li {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.listIcon {
  font-size: 1.5rem;
  color: #4A90E2;
  margin-right: 10px;
}

.SliderWrapper {
  width: 100%;
  height: 400px;
  padding: 20px;
  overflow: hidden;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
.mainContent {
  padding: 40px;
}

.aboutSection, .researchSection, .contactSection {
  margin-bottom: 40px;
}

.sectionTitle {
  font-size: 2em;
  color: #004080;
  margin-bottom: 20px;
}

.sectionText {
  font-size: 1.1em;
  line-height: 1.6;
  color: #333;
}

.card {
  height: 100%;
}

.cardTitle {
  font-size: 1.2em;
  color: #004080;
}

.cardText {
  font-size: 1em;
  color: #333;
}

.contactItem {
  display: flex;
  align-items: center;
}

.contactIcon {
  margin-right: 8px;
}

.contactText {
  font-size: 1.1em;
  color: #333;
}















.introduction {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  align-items: start;
  justify-content: start;
  padding: 0 305px;
  background-image:  repeating-radial-gradient( circle at 0 0, transparent 0, #f7f4e598 40px ), repeating-linear-gradient( #53546331, #bdbdc767 );
}

@media (max-width: 991px) {
  .container {
    padding: 50px 70px;
  }
}

.article {
  align-self: center;
  z-index: 0;
  display: flex;
  width: 100%;
  max-width: 1300px;
  flex-direction: column;
  padding: 119px 28px 119px 0;
}

@media (max-width: 991px) {
  .article {
    max-width: 100%;
    padding: 0 20px 100px 0;
  }
}

.headerContent {
  display: flex;
  width: 802px;
  max-width: 100%;
  gap: 40px 100px;
  color: #000;
  flex-wrap: wrap;
}

.heading {
  position: relative;
  display: flex;
  padding-left: 25px;
  flex-direction: column;
  white-space: nowrap;
  justify-content: start;
  font: 700 60px/1.3 Roboto, -apple-system, Roboto, Helvetica, sans-serif;
}

@media (max-width: 991px) {
  .heading {
    font-size: 40px;
    white-space: initial;
  }
}

.headingBackground {
  background-color: #072d60;
  position: absolute;
  z-index: 0;
  display: flex;
  min-height: 45px;
  width: 5px;
  left: 0;
  top: 15px;
  height: 45px;
}

.welcomeText {
  z-index: 0;
}

@media (max-width: 991px) {
  .welcomeText {
    font-size: 40px;
  }
}

.subHeading {
  flex-grow: 1;
  width: 367px;
  margin: auto 0;
  font: 900 21px Noto Sans KR, -apple-system, Roboto, Helvetica, sans-serif;
}

@media (max-width: 991px) {
  .subHeading {
    max-width: 100%;
  }
}

.description {
  flex-grow: 1;
  width: 1135px;
  align-self: end;
  max-width: 100%;
  gap: 40px 100px;
  color: #535353;
  flex-wrap: wrap;
  margin: 38px 0 -28px;
  font: 350 18px/35px Noto Sans KR, -apple-system, Roboto, Helvetica, sans-serif;
}

@media (max-width: 991px) {
  .description {
    max-width: 100%;
    margin-bottom: 10px;
  }
}

.backgroundImage {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: right;
  mix-blend-mode: multiply;
}
