.membersNavigation {
  background-color: rgba(0, 0, 0, 0.2);
  color: #fff;
  text-align: center;
  padding: 0 70px;
  position: relative;
  top: -68px;
  width: 100%;
}

.navigationTitle {
  font: 600 75px Poppins, sans-serif;
  margin-bottom: 20px;
}

.navigationList {
  display: flex;
  justify-content: flex-start;
  list-style-type: none;
  padding: 0;
  margin: 0;
  font: 500 18px 'Noto Sans KR', -apple-system, Roboto, Helvetica, sans-serif;
}

.navigationItem {
  padding: 23px 70px;
  cursor: pointer;
}

.navigationItem.active {
  background-color: #fff;
  color: #000;
}

@media (max-width: 291px) {
  .membersNavigation {
    padding: 20px;
    margin-top: 40px;
  }

  .navigationTitle {
    font-size: 40px;
  }

  .navigationList {
    flex-direction: column;
  }

  .navigationItem {
    padding: 15px 20px;
  }
}

@media (max-width: 991px) {
  .membersNavigation {
    top: 0px !important;
  }
}