/* Global Styles */
/* 기본 설정 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

:root {
  --HeaderGradientFirst: #CFC6E8;
  --HeaderGradientSecond: #1A1A2E;
  --HeaderGradientAngle: 90deg;
  --gradientFirst: #ECECEC;
  --gradientSecond: #B4B4B4;
  --HeaderlogoHighlight: #FFD700;
  --HeaderColor: #e3e3e3;
  --HeroGradientFirst: #CFC6E8;
  --HeroGradientSecond: #1A1A2E;
  --NormalColor: #2c2c2c;
  --NormalBackground: #2c2c2c;
  --NormalWeakColor: #727272;
  --NormalButtonColor: #525252;
  --NormalIconFilter: invert(0);
  --NormalButtonInnerColor: #ffffff;
  --NormalWeakestColor: #929292;
}


/* font import */
@import './fonts.css';
