.container {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  align-items: start;
  justify-content: start;
  padding: 0 305px;
  background: linear-gradient(45deg, #ffffff, #bfe5ff54);
}

@media (max-width: 991px) {
  .container {
    padding: 50px 70px;
  }
}

.article {
  align-self: center;
  z-index: 0;
  display: flex;
  width: 100%;
  max-width: 1300px;
  flex-direction: column;
  padding: 119px 28px 119px 0;
}

@media (max-width: 991px) {
  .article {
    max-width: 100%;
    padding: 0 20px 100px 0;
  }
}

.headerContent {
  display: flex;
  width: 802px;
  max-width: 100%;
  gap: 40px 100px;
  color: #000;
  flex-wrap: wrap;
}

.heading {
  position: relative;
  display: flex;
  padding-left: 25px;
  flex-direction: column;
  white-space: nowrap;
  justify-content: start;
  font: 700 60px/1.3 Roboto, -apple-system, Roboto, Helvetica, sans-serif;
}

@media (max-width: 991px) {
  .heading {
    font-size: 40px;
    white-space: initial;
  }
}

.headingBackground {
  background-color: #072d60;
  position: absolute;
  z-index: 0;
  display: flex;
  min-height: 45px;
  width: 5px;
  left: 0;
  top: 15px;
  height: 45px;
}

.welcomeText {
  z-index: 0;
}

@media (max-width: 991px) {
  .welcomeText {
    font-size: 40px;
  }
}

.subHeading {
  flex-grow: 1;
  width: 367px;
  margin: auto 0;
  font: 900 21px Noto Sans KR, -apple-system, Roboto, Helvetica, sans-serif;
}

@media (max-width: 991px) {
  .subHeading {
    max-width: 100%;
  }
}

.description {
  flex-grow: 1;
  width: 1135px;
  align-self: end;
  max-width: 100%;
  gap: 40px 100px;
  color: #535353;
  flex-wrap: wrap;
  margin: 38px 0 -28px;
  font: 350 18px/35px Noto Sans KR, -apple-system, Roboto, Helvetica, sans-serif;
}

@media (max-width: 991px) {
  .description {
    max-width: 100%;
    margin-bottom: 10px;
  }
}

.backgroundImage {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: right;
  mix-blend-mode: multiply;
}
