.researcherCard {
  background-color: #fff;
  border: 1px solid #ddd;
  display: flex;
  padding: 0 36px 0 1px;
  position: relative;
}
.researcherCard:hover {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1) !important;
  transform: translateY(-5px);
  transition: all 0.3s;
  filter: brightness(1.05);
}

.imageWrapper {
  border-right: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1px 0;
}

.researcherImage {
  aspect-ratio: 0.81;
  object-fit: contain;
  object-position: center;
  width: 219px;
}

.researcherInfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: auto 0;
  margin-left: 30px;
  font: 16px Noto Sans KR, sans-serif;
}

.researcherName {
  color: #00407c;
  font-size: 27px;
  font-weight: 700;
  line-height: 1;
}

.researcherTitle {
  color: val(--NormalWeakColor);
  font-weight: 500;
  line-height: 1;
  margin-top: 9px;
}

.researcherDetails {
  border-top: 1px solid var(--NormalWeakestColor);
  align-self: stretch;
  display: flex;
  margin-top: 26px;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  padding: 15px 30px 4px 0;
}

.detailItem {
  display: flex;
  gap: 12px;
  margin-bottom: 15px;
}

.icon {
  aspect-ratio: 1;
  object-fit: contain;
  object-position: center;
  width: 27px;
}

.detailLabel {
  color: #494949;
  font-weight: 700;
  line-height: 1;
  margin: auto 0;
}

.detailValue {
  color: #333;
  font-weight: 300;
  margin: auto 0;
  flex-basis: auto;
}

@media (max-width: 1170px) {
  .researcherCard {
    max-width: 100%;
    margin-top: 30px;
    padding-right: 20px;
  }

  .researcherDetails {
    padding-right: 20px;
  }
}


.cvWrapper {
  position: absolute;
  top: 10px; /* 연구 카드 오른쪽 맨 위에 붙이기 위한 설정 */
  right: 10px;
  background-color: #ffffff; /* 배경색 설정 (필요시 조정) */
  border: 1px solid #dddddd; /* 테두리 설정 (필요시 조정) */
  padding: 5px;
  z-index: 10; /* 다른 요소들 위에 보이도록 설정 */
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cvWrapper:hover {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1) !important;
  transition: all 0.3s;
  background-color: #c5c5c5;
  
}

.cvIcon {
  aspect-ratio: 1;
  object-fit: contain;
  object-position: center;
  width: 30px;
  height: 30px;
  margin: auto;
  filter: invert(70%);
}

.cvWrapper:hover .cvIcon {
  filter: invert(0%);
  transition: all 0.3s;
}

