.people {
  background-color: #3a3a3a;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  justify-content: flex-start;
  padding: 174px 100px;
}

@media (max-width: 991px) {
  .people {
    padding: 100px 20px;
  }
}

.container {
  background-color: #584fd9;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  color: #f8c099;
  text-transform: uppercase;
  font: 600 96px/1 Poppins, sans-serif;
}

@media (max-width: 991px) {
  .container {
    font-size: 40px;
  }
}

.heroSection {
  display: flex;
  flex-direction: column;
  background-blend-mode: soft-light;
  position: relative;
  min-height: 247px;
  width: 100%;
  align-items: start;
  padding: 148px 80px 290px;
}

@media (max-width: 991px) {
  .heroSection {
    max-width: 100%;
    font-size: 40px;
    padding: 100px 20px;
  }
}

.backgroundImage {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.contentWrapper {
  position: relative;
  display: flex;
  margin-bottom: -58px;
  width: 746px;
  max-width: 100%;
  flex-direction: column;
}

@media (max-width: 991px) {
  .contentWrapper {
    margin-bottom: 10px;
    font-size: 40px;
  }
}

.decorativeLine {
  background-color: #f8c099;
  width: 125px;
  height: 13px;
}

.title {
  margin-top: 41px;
}

@media (max-width: 991px) {
  .title {
    max-width: 100%;
    margin-top: 40px;
    font-size: 40px;
  }
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}