.newVenn {
  position: relative;
  width: 300px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sector {
  width: 200px;
  height: 200px;
  background: conic-gradient(#3498db 0deg 120deg, transparent 120deg 360deg);
  border-radius: 50%;
  position: absolute;
  transition: transform 0.3s ease, background 0.3s ease;
}

/* 마우스를 올렸을 때 색상과 크기 변경 */
.sector:hover {
  background: conic-gradient(#2ecc71 0deg 120deg, transparent 120deg 360deg);
  transform: scale(1.1);
}
